(function($) {
  $(document).ready(function() {
    // Floating header 
    toggleFloatingHeader(); // trigger right away in case saved position reload
    $(document).on('scroll', function() {
      toggleFloatingHeader();
    })

    // Back to top
    $('#backToTop').on('click', function() {
      window.scrollTo({top: 0, behavior: 'smooth'});
    })

    // Overlay
    $('#overlay').on('click', function(e) {
      const targetParent = $(e.target).closest('.overlay-content');
      if (targetParent.length === 0) {
        toggleOverlay(false);
      }
    });

    $('#consultation-btn-1, #consultation-btn-2').on('click', function() {
      toggleOverlay(true);
    });

    function toggleFloatingHeader() {
      const scrollPosition = $(document).scrollTop();
      const heroHeight = $('#hero').height();
      if (scrollPosition >= heroHeight) {
        $('#header').addClass('floating')
      }
      else {
        $('#header').removeClass('floating')
      }
    }
    
    function toggleOverlay(open) {
      if (open) {
        $('body').addClass('overflow-none')
        $('#overlay').addClass('active');
      }
      else {
        $('body').removeClass('overflow-none')
        $('#overlay').removeClass('active');
      }
    }

  });
})(jQuery);
